<template>
  <div @click="CancleMask">
    <div class="CHheader">
      <p @click="onSkip('city')">{{defaultCity}} <span class="iconfont iconxiangxiajiantou" style="font-size:8px"></span></p>
      <h2 style="color:black;font-size:18px">影院</h2>
      <span @click="onSkip('search')" style="font-size:25px" class="iconfont iconsousuo"></span>
    </div>
    <div class="CHmenu">
      <p @click.stop="()=>AreaShow=!AreaShow" :class="{active:AreaShow}">全城 <span class="iconfont iconxiangxiajiantou" :class="{tranformRotate:AreaShow}" style="font-size:8px"></span> </p>
      <h4 style="color:black">APP订票</h4>
      <span>最近去过</span>
    </div>
    <div class="cinemaListClass">
      <ul>
        <li v-for="cin in AreaCinemaList" :key="cin.cinemaId" @click="ToCinemaDetils(cin.cinemaId)">
          <div class="cinemaName">
            <h4>{{cin.name}}</h4>
            <p>{{cin.address}}</p>
          </div>
          <div class="cinemaListPrice">
            <h5>￥{{cin.lowPrice | abandon}} 起</h5>
            <span>距离未知</span>
          </div>
        </li>
      </ul>
    </div>
    <div class="AreaClass" v-if="AreaShow">
      <ul>
        <li v-for="AreaText in putOffRepeat" :key="AreaText" :class="{active:AreaText===defaultArea}" @click="changeArea(AreaText)">
          {{AreaText}}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapState,mapMutations } from "vuex";
import { instance } from "@/utils/http";
export default {
  data() {
    return {
      cinemaList: [],
      AreaShow:false,
      defaultArea:'全城'
    };
  },
  methods: {
    ...mapMutations("TabbarStore", ["changeIsTabbar"]),
    ToCinemaDetils(id){
      this.$router.push('/cinemaDetils/'+id)
    },
    onSkip(path){
      this.$router.push(path)
    },
    changeArea(Area){
      this.defaultArea = Area
      this.AreaShow = false
      if(Area === '全城'){
        return this.cinemaList
      }
    },
    CancleMask(){
      this.AreaShow = false
    }
  },
  computed:{
    ...mapState('cityStore',['defaultCity','defaultCityId']),
    AreaCinemaList(){
      if(this.defaultArea === '全城'){
        return this.cinemaList
      }
      return this.cinemaList.filter(item=>this.defaultArea===item.districtName)
    },
    putOffRepeat(){
      let newArr = this.cinemaList.map(item=>{
        return item.districtName;
      })
      return ["全城" , ...Array.from(new Set(newArr))];
  },
  },
  created() {
    this.changeIsTabbar(true);
    instance(`/gateway?cityId=${this.defaultCityId}&ticketFlag=1&k=4228693`, {
      headers: {
        "X-Host": "mall.film-ticket.cinema.list"
      }
    }).then(res => {
      this.cinemaList = res.data.data.cinemas;
    });
  }
};
</script>
<style lang="scss">
.AreaClass{
  width: 100%;
  position: fixed;
  top: 88px;
  z-index: 101;
  background: rgba(0,0,0,.2);
  height: 100%;
  ul{
    background: white;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    padding-top: 10px;
    li{
      margin-left: 10px;
      margin-bottom: 10px;
      font-size: 12px;
      width: calc((100vw - 50px)/4);
      color: #797d82;
      border-radius: 3px;
      text-align: center;
      height: 30px;
      line-height: 27px;
      border: 1px solid rgba(210,214,220,.5);
    }
    .active{
      border: 1px solid #ff5f16;
      color: #ff5f16;
    }
  }
}
.cinemaListClass {
  width: 100%;
  overflow-y: scroll;
  margin-top: 88px;
  box-sizing: border-box;
  ul {
    box-sizing: border-box;
    width: 100%;
    li {
      border-bottom: 1px solid #ededed;
      padding: 15px;
      display: flex;
      justify-content: space-between;
      .cinemaName {
        h4 {
          color: #191a1b;
          font-size: 15px;
        }
        p {
          color: #797d82;
          font-size: 12px;
          margin-top: 5px;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          width: 2rem;
        }
      }
      .cinemaListPrice {
        h5{
          font-size: 15px;
          color: #ff5f16;
        }
        span{
          font-weight: 400;
          display: block;
          margin-top: 5px;
          font-size: 11px;
          color: #797d82;
        }
      }
    }
  }
}
.CHheader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 44px;
  padding: 0 15px;
  position: fixed;
  top: 0;
  background: white;
  z-index: 1;
  .iconxiangxiajiantou{
    transform: scale(0.6);
    display: inline-block;
    font-weight: bold;
  }
}
.CHmenu {
  background: white;
  z-index: 1;
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 44px;
  padding: 0 15px;
  position: fixed;
  top: 44px;
  border-bottom: 1px solid #ededed;
  .iconxiangxiajiantou{
    transform: scale(0.6);
    display: inline-block;
    font-weight: bold;
  }
  .active{
      color: #ff5f16;
    }
    .tranformRotate{
      transform: rotate(180deg) scale(0.6);
      font-weight: bold;
    }
}
</style>